<template>
  <div class="overall">
    <div class="back" @click="back">
      <svg t="1616995826666" class="icon" viewBox="0 0 1354 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="4717" width="32" height="32"><path d="M1091.899077 395.0592C1171.771077 442.683077 1269.76 571.092677 1310.530954 725.700923c40.770954 154.592492 34.028308 293.037292 34.028308 293.037292s-52.791138-85.244062-74.200616-115.129107c-21.393723-29.9008-111.584492-141.248985-251.557415-205.776739-139.988677-64.543508-373.9648-53.0432-373.9648-53.0432v268.729108L0 456.767015 644.836431 0v271.202462s154.907569 11.657846 235.882338 28.435692c136.381046 28.246646 211.180308 95.421046 211.180308 95.421046z" fill="#1296db" p-id="4718"></path></svg>
    </div>
    <el-breadcrumb separator="|" class="bread" v-if="showbread">
      <!-- javascript: void is for avoid redirect -->
      <el-breadcrumb-item class="bread-item" @click.native="pass(0)"><a href="javascript:void(0)" ref="m0">70-74</a></el-breadcrumb-item>
      <el-breadcrumb-item class="bread-item" @click.native="pass(1)"><a href="javascript:void(0)" ref="m1">75-79</a></el-breadcrumb-item>
      <el-breadcrumb-item class="bread-item"><a href="javascript:void(0)" ref="m2">80-84</a></el-breadcrumb-item>
      <el-breadcrumb-item class="bread-item"><a href="javascript:void(0)" ref="m3">85-89</a></el-breadcrumb-item>
      <el-breadcrumb-item class="bread-item"><a href="javascript:void(0)" ref="m4">90+</a></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-carousel indicator-position="outside" :autoplay="false" height="500px">
      <ausmap :range='range'></ausmap>
    </el-carousel>
  </div>
</template>

<script>
import map from "@/components/charts/ausmap";

export default {
  name: "vis",
  data() {
    return {
      showbread: true,
      range: 0,
      m: [0,1,2,3,4]
    }
  },
  methods: {
    pass(num) {
      // make other item to black color, except one the user clicked
      this.m.forEach(item => {
        if(item !== num) {
          this.$refs['m' + item].style.color = '#303133'
          // this.$refs['m' + item].style.transform = 'scale(1)'
        }
      })
      this.$refs['m' + num].style.color = 'red'
      // this.$refs['m' + num].style.transform = 'scale(1.2)' 
      this.range = num
    },
    back() {
      this.$router.push('/')
    }
  },
  // register components
  components: {
    'ausmap': map,
  },
};
</script>
<style scoped>
.overall {
  position: relative;
  width: 800px;
  height: 500px;
  left: 50%;
  transform: translate(-50%);
  /* z-index: 9; */
}
.bread {
  position: absolute;
  z-index: 100;
}
.bread-item {
  cursor: pointer;
}
.back {
  cursor: pointer;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  position: absolute;
  left: -100px;
  top: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 5px 0 #4444;
}
</style>